import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import LockIcon from '@material-ui/icons/LockOutlined';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';

import server  from '../config';
import axios from 'axios';
import AuthService from '../components/AuthService';

const styles = theme => ({
  main: {
    width: 'auto',
    display: 'block', // Fix IE 11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
});

class SignIn extends Component {

  constructor (props){
      super(props);
      this.state = {
          email: '',
          password: ''
      };
      this.Auth = new AuthService();
  }

  Auth = new AuthService();

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };
  
  handleClick = () => {
        this.Auth.login(this.state.email, this.state.password).then(res =>{
            console.log(res);
            this.props.history.push('/home');
         })
         .catch(err =>{
            console.log(err);
         })
  }

  handleKeyPress = event => {
    if (event.key === 'Enter') {
      this.handleClick();
    }
  };
  
  render () {

    const { classes } = this.props;

    // if (this.Auth.loggedIn()){
    //     return <Redirect to={'/home'} />;
    // } 
    
    return (
        <main className={classes.main}>
        <CssBaseline />
        <Paper className={classes.paper}>
            <Avatar className={classes.avatar}>
            <LockIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
            Sign in
            </Typography>
            <form className={classes.form}
                >
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="email">Email Address</InputLabel>
                    <Input id="email" name="email" autoComplete="email" value={this.state.email} autoFocus onChange={this.handleChange('email')} onKeyPress={this.handleKeyPress}/>
                </FormControl>
                <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="password">Password</InputLabel>
                    <Input name="password" type="password" id="password" value={this.state.password} autoComplete="current-password" onChange={this.handleChange('password')} onKeyPress={this.handleKeyPress}/>
                </FormControl>
                {/* <FormControlLabel
                    control={<Checkbox value="remember" color="primary" />}
                    label="Remember me"
                /> */}
                <Button
                    fullWidth 
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    onClick={() => this.handleClick()}
                >
                    Sign in
                </Button>
            </form>
        </Paper>
        </main>
    );
    }
}

SignIn.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SignIn);